.wrapper {
    display: flex;
    justify-content: space-around;
    margin-top: 103px;
}
.productCard{
    grid-template-columns:33%
}
.productCard:hover {
    background-color: #fff;
    filter: drop-shadow(0px 4px 35px rgba(168, 172, 176, 0.19));
    box-shadow: 0px 4px 35px rgba(168, 172, 176, 0.19);
  }  

.productImg img{
    position: relative;
    height: 330px;
    width: 354px;
    object-fit:cover;
}
.productName {
    color:#1d1f22;
    font-size: 18px;
	 line-height: 28px; 
	margin: 24px 0px 5px 0px;
}
.productPrice{
    color:#1d1f22;
    font-size: 20px;
	line-height: 28px;
	margin: 0px 0px 10px 0px;
}  
  
  
  
  
  
  
